.gpt3__possibility {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
}

.gpt3__possibility-image img {
    width: 100%;
    height: 100%;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--color-text);
}

.gpt3__possibility-content h4:last-child {
    color: #FF8A71;
    margin-top: 1rem;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: 1rem;
}

.gpt3__possibility-content p {
    margin-top: 1rem;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--color-text);

}

@media screen and (max-width: 960px) {
    .gpt3__possibility {
        flex-direction: column;
    }

    .gpt3__possibility-image {
        margin: 1rem 0;
    }

    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}